@import url(https://fonts.googleapis.com/css2?family=Mukta:wght@400;700&display=swap);
body{margin:0}.App{display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:#373737;font-family:'Mukta', sans-serif}.App__head{color:aqua;border:3px solid aqua;padding:10px}

.comments{color:#001919;border:3px solid #001919;margin:10px;border-radius:20px}.comments__list{padding:5px}.comments__list>p{color:#384332}

.name{color:#654321}

.info{margin-top:40px;margin-bottom:40px;border-radius:20px}.info__items{padding:15px}.info__items-frame{background-color:#10A5F5;border-radius:15px;color:#373737}.info__items-frame>div{padding:20px}

.list{list-style-type:none;padding:0 40px}.list__item{border-radius:30px;background-color:aqua}

